.GroupList-div{
    background-color: var(--blue);
    box-shadow:4px 4px 20px var(--boxshadow);
    border-radius: 20px;
    padding:15px;
    font-size: 1rem;
    width: 15rem;
    margin: 40px 20px 20px ;
}

.delete-button,
.Settings-button{
    height: 1rem;
}

.button-div{
    display: flex;
    justify-content: space-between;
}

.delete-button:hover,
.Settings-button:hover{
    scale: 1.2;
}

.delete-button:active,
.Settings-button:active{
    scale: 0.8;
}

.group-title h3{
    font-size: 1.2rem;
    overflow: hidden;
    text-overflow:ellipsis;
    margin:5px;
    max-height: 3.5rem;
}

.topic-form input{
    border-radius: 20px;
    border: 1px solid var(--pink);
    background-color: var(--blue);
    color:var(--white);
    width: 70%;
    margin-top: 1rem;
    outline: none;
    margin-bottom: 7px;
}

.topic-form button{
    border-radius: 20px;
    border: 1px solid var(--pink);
    background-color: var(--blue);
    color:var(--pink);
    font-size: .8rem;
    padding:.2rem 1rem;
}

.topic-form button:hover,
.topic-form input:hover{
    box-shadow: 3px 2px 4px var(--pink);
    cursor: pointer;
}

.topic-form input::placeholder{
    color:var(--white)
}

.orange{
    color:var(--orange)
}

.yellow{
    color:var(--yellow)
}

.green{
    color:var(--green)
}

.item-check{
    display: flex;
    justify-content: center;
    align-items:center;
    height: 2rem;
}

.item-check img{
    margin:0 0.5rem;
}

.item-check-div{
    display: flex;
    justify-content: center;
    align-items:center;
    cursor: pointer;
}


